<template>
  <div class='user-form'>
    <h1 style='text-align: center; font-size: 30px; margin: 20px auto;'>{{ isLogin ? 'Login' : 'Register' }}</h1>
    <ValidationObserver ref='logIn' v-slot='{ invalid }'>
      <form @submit.prevent='send'>
        <div class='form-field'>
          <input
            v-if='!isLogin'
            id='username-input'
            v-model.lazy='user.name'
            autocomplete='off'
            placeholder='Name'
            type='text'
          />
        </div>
        <div class='form-field'>
            <input
              id='username-input'
              v-model.lazy='user.email'
              autocomplete='off'
              placeholder='Email'
              type='text'
            />
        </div>
        <div class='form-field'>
            <input
              id='phone-input'
              v-model.lazy='user.password'
              autocomplete='off'
              placeholder='Password'
              type='text'
            />
        </div>
        <div class='form-field'>
          <input
            v-if='!isLogin'
            v-model.lazy='user.password_confirmation'
            autocomplete='off'
            name='phonenumber'
            placeholder='Confirmation Password'
            type='text'
          />
        </div>
        <button type='submit'>Send</button>
        <p v-if='isLoginError' class='error-message'>Login failed</p>
      </form>
    </ValidationObserver>
    <v-btn @click='change' style='color: burlywood; margin-top: 10px' outlined>{{ !isLogin ? 'To Login' : 'To Register' }}</v-btn>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'LoginForm',
  data() {
    return {
      user: {
        name: '',
        email: '',
        password: '',
        password_confirmation: ''
      },
      isLoginError: false,
      isLogin: true
    }
  },
  methods: {
    ...mapActions({
      loginUser: 'logIn',
      registation: 'reg'
    }),
    send() {
      this.isLogin ? this.logIn() : this.reg()
    },
    async logIn() {
        let userData = {
          email: this.user.email.trim(),
          password: this.user.password.trim()
        }
        this.isLoginError = await this.loginUser(userData)
    },
    async reg() {
        let userData = {
          name: this.user.name.trim(),
          email: this.user.email.trim(),
          password: this.user.password.trim(),
          password_confirmation: this.user.password_confirmation.trim()
        }
        this.isLoginError = await this.registation(userData)
    },
    change(){
      this.isLogin = !this.isLogin
    }
  },
  watch: {
    isLoginError() {
      if (this.isLoginError) {
        setTimeout(() => (this.isLoginError = false), 3000)
      }
    }
  }
}
</script>

